define('viewmodel',['require','exports','module','knockout','js/core/clock','js/core/news'],function (require, exports, module) {
    var viewModel,
        ko = require('knockout'),
        clock = require('js/core/clock'),
        news = require('js/core/news');

    viewModel = function (ko, clock, news) {
        var self = this,
            itemCount = 0;

        /*Clock*/
        self.time = ko.observable();
        self.date = ko.observable();
        /*News*/
        self.newsItems = ko.observableArray();
        self.newsItemsCount = ko.observable();

        /*Clock*/
        function updateClock() {
            self.time(clock.time());
            self.date(clock.date());
        }

        setInterval(updateClock(), 1000);

        /*News*/
        function setNewsItems(items, count) {
            self.newsItemsCount(items.length);
            itemCount += count;

            for (var i = 0; i < count; i++) {
                //console.log(self.newsItemsCount());
                //console.log(itemCount);
                self.newsItems.push(items[i]);
            }
        }

        news.success(function (data) {
            setNewsItems(data.responseData.feed.entries, 5);
        });
    };

    exports.viewmodel = viewModel(ko, clock, news);
});
